body{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Noto Sans KR', sans-serif;
    /* font-family: 'East Sea Dokdo', sans-serif;
    font-family: 'Nanum Gothic', sans-serif;
    font-family: 'Noto Sans KR', sans-serif; */
    }
  
  a {
    text-decoration: none; /* 링크 밑줄 제거 */
  }
  
  /* Nav 스타일링 */
  nav {
    margin-bottom: 0;
    height: 120px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0); /* 배경색 */
    border-bottom: 1px solid rgba(255,255,255,.3);
    display: flex; /* 가로 배치 */
    align-items: center; /* 세로 중앙 정렬 */
    justify-content: space-between;
    font-size: 16px;
    font-weight: 500;
    transition: all 0.3s ease;
    position: fixed;
    top: 0;
    z-index: 1;
  }
  
  #nav-box1{
    font-size: 92px;
    font-family: 'East Sea Dokdo', sans-serif;
    color: #fff;
    margin-left: 7vw;
    display: flex; /* 가로 배치 */
    align-items: center;
    z-index: 20;
  }
  
  /* .nav-box1-size{
    margin-left: 10px;
    padding-top: 6px;
  } */
  
  #logo-name{
    width: auto;
    height: 48px;
  }
  
  #nav-box2{
    display: flex;
    align-items: center;
    margin-right: 7vw;
  }
  
  .nav-text{
    font-size: 22px;
    font-weight: 700;
    margin: 0 40px;
    color: #fff;
  }
  
  /* 추가된 스크롤 이벤트에 따른 스타일 */
  nav.scrolled {
    background-color: #fff !important;
    border-bottom: 1px solid #e0e0e0;
  }
  
  nav.scrolled #nav-box1 {
    color: #000 !important;
  }
  
  nav.scrolled .nav-text {
    color: #000 !important;
  }
  
  #nav-box1.open{
    color: #000;
    transition: all 0.8s ease;
  }
  
  
  .burger-menu {
    display: none;
    cursor: pointer;
    flex-direction: column;
    justify-content: space-between;
    height: 20px;
    z-index: 2;
    margin-right: 4vw;
  }
  
  .burger-line {
    background-color: #fff;
    height: 2px;
    width: 32px;
    border-radius: 20px;
  }
  
  .menu-box-container {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 100%; /* initially off-screen to the right */
    height: 100%;
    width: 100%;
    background-color: #fff;
    z-index: 1;
    transition: left 0.5s ease; /* transition for the left property */
  }
  
  .menu-box-container.open {
    left: 0;
  }
  
  .burger-line.open {
    background-color: #000;
  }
  
  nav.scrolled .burger-line{
    background-color: #000;
  }
  
  .mobile-menu {
    display: flex;
    flex-direction: column;
    align-items: left;
  }
  
  .mobile-menu a {
    font-size: 20px;
    font-weight: 700;
    color: #000;
    padding: 30px 20px;
    border-bottom: 1px solid #ccc;
  }
  
  #nav-text-size{
    margin-top: 100px;
    border-top: 1px solid #ccc;
  }
  
  @media screen and (max-width: 1660px) {
    .nav-text{
      font-size: 20px;
      margin: 0;
      margin-left: 50px;
    }
  
    #nav-box1{
      font-size: 72px;
      margin-left: 4vw;
    }
  
    #nav-box2{
      margin-right: 4vw;
    }
  
    #logo-name{
      width: auto;
      height: 44px;
    }
  }
  
  /* Mobile Styles */
  @media screen and (max-width: 1000px) {
    #nav-box2 {
      display: none;
    }
  
    .burger-menu {
      display: flex;
    }
  
    #nav-box1{
      font-size: 62px;
      margin-left: 4vw;
    }
  
    #logo-name{
      width: auto;
      height: 36px;
    }
  
    nav {
      height: 100px;
    }
  
  }
  
  @media screen and (max-width: 644px) {
  
    #nav-box1{
      font-size: 52px;
      margin-left: 4vw;
    }
  
    #logo-name{
      width: auto;
      height: 32px;
    }
  
    nav {
      height: 80px;
    }
  
    .burger-menu {
      height: 18px;
      width: auto;
      z-index: 2;
      margin-right: 4vw;
    }
  
    .burger-line {
      width: 28px;
    }
  
    #nav-text-size{
      margin-top: 80px;
    }
  }